import {defineStore} from 'pinia'
import { ref } from 'vue'
import { useLocalStorage } from '@vueuse/core'


export const useLoginStore = defineStore('login',()=>{
    let token = ref(useLocalStorage('token-store',{},{
        mergeDefaults:true,

    }))
    const sharedEmail = ref()
    const isUserLoggedIn = ref(false)
    async function setToken(tokenArgs){
        console.log('before useLocalStorage',tokenArgs)
        token = ref(useLocalStorage('token-store', {
            token: tokenArgs
        }, {
            mergeDefaults: true
        }))
        console.log('after useLocalStorage',token)
    }
    function setEmail(email){
        sharedEmail.value = email
    }

    function setLoginState(bool){
        isUserLoggedIn.value = bool
    }
    return {token,sharedEmail,setToken,setEmail,setLoginState,isUserLoggedIn}
})

export const useAuthenticationTypeStore = defineStore('AuthType',()=>{
    const isLoginForm = ref(false)
    return {isLoginForm}
})